.chat-wrap {
  position: absolute;
  height: 100%;
  width: 100%;
  max-width: 1250px;
  left: 0;
  right: 0;
  margin: 0 auto;
  padding: 30px 20px;
}

.chat {
  height: 100%;
  background: #fff;
  border-radius: 2px;
  border: 1px solid #e9e9eb;

  &__header {
    background: #fbfbfc;
    border-bottom: 1px solid #e9e9ec;

    &:after {
      clear: both;
      display: table;
      content: " ";
    }

  }

  &__tab {
    float: left;
    padding: 0 15px;

    &:after {
      clear: both;
      display: table;
      content: " ";
    }

  }

  &__tab-item {
    position: relative;
    bottom: -1px;
    display: inline-block;
    float: left;
    padding: 24px 15px 21px 15px;
    font-size: 15px;
    font-weight: 500;
    text-transform: uppercase;
    color: #888891;
    border-bottom: 1px solid inherit;
    cursor: pointer;
    transition: all .2s;
    user-select: none;

    &:hover {
      color: #1b1b27;
    }

    &_active {
      color: #1b1b27;
      border-bottom: 2px solid #f64a35;
    }

  }

  &__search-all {
    position: relative;
    float: right;
    margin-right: 30px;
    padding: 17px 0 0 0;
    cursor: pointer;

    &:after {
      content: "";
      position: absolute;
      top: 23px;
      right: 0;
      background: url("../images/search.png");
      width: 18px;
      height: 18px;
    }

    & input {
      width: 100px;
      padding: 7px 20px;
      margin-right: 20px;
      font-size: 15px;
      font-weight: 400;
      color: #1b1b27;
      border: 0;

      &::-webkit-input-placeholder {
        color: #888891 !important;
      }

      &::-moz-placeholder { /* Firefox 18- */
        color: #888891 !important;
      }

      &::-moz-placeholder {  /* Firefox 19+ */
        color: #888891 !important;
      }

      &:-ms-input-placeholder {
        color: #888891 !important;
      }

    }

  }

  &__container {
    height: calc(100% - 65px);
  }

  &__cl {
    float: left;
    width: 400px;
    height: 100%;
    border-right: 1px solid #eaeaed;
  }

  //&__cr {
  //  display: table;
  //  border-collapse: collapse;
  //  position: relative;
  //  width: calc(100% - 400px);
  //  margin-left: 400px;
  //  height: 100%;
  //}

  &__search {
    position: relative;
    cursor: pointer;
    border-bottom: 1px solid #eaeaed;

    &:after {
      content: "";
      position: absolute;
      top: 21px;
      left: 30px;
      background: url("../images/search.png");
      width: 18px;
      height: 18px;
    }

    & input {
      width: 100%;
      padding: 21px 21px 21px 58px;
      font-size: 15px;
      font-weight: 400;
      color: #1b1b27;
      border: 0;
      outline: 0;

      &::-webkit-input-placeholder {
        color: #888891 !important;
      }

      &::-moz-placeholder { /* Firefox 18- */
        color: #888891 !important;
      }

      &::-moz-placeholder {  /* Firefox 19+ */
        color: #888891 !important;
      }

      &:-ms-input-placeholder {
        color: #888891 !important;
      }

    }

  }

  &__list {
    height: calc(100% - 59px);
    overflow: auto;
  }

  &__list-item {
    height: 108px;
    padding: 26px 29px 26px;
    border-bottom: 1px solid #eaeaed;
    cursor: pointer;
    transition: all .2s;
    user-select: none;

    &:after {
      clear: both;
      display: table;
      content: " ";
    }

    &:hover {
      background: #fbfbfc;
    }

    &:active {
      background: #f4f4f5;
    }

    &_active {
      position: relative;

      &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        height: 108px;
        width: 3px;
        background: #f64a35;
      }

    }

    &_pin {
      position: relative;

      &:after {
        content: "";
        position: absolute;
        top: 28px;
        right: 31px;
        background: url("../images/pin.png") no-repeat;
        width: 10px;
        height: 16px;
        transform: rotate(45deg);
      }

    }

    &__photo {
      float: left;
      width: 56px;

      & > img {
        border-radius: 50%;
        width: 56px;
        height: 56px;
        vertical-align: top;
      }

      &_dot {
        position: relative;

        &:after {
          content: "";
          position: absolute;
          bottom: 0;
          right: 0;
          width: 8px;
          height: 8px;
          border-radius: 50%;
          border: 2px solid #fff;
        }

      }

      &_green {

        &:after {
          background: #4cbd41;
        }

      }

      &_blue {

        &:after {
          background: #29a9ff;
        }

      }

    }

    &__content {
      margin-left: 76px;
    }

    &__who {
      max-width: 100%;
      padding-right: 20px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    &__name {
      max-width: 100%;
      padding-right: 10px;
      font-size: 15px;
      font-weight: 500;
      color: #1b1b27;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      vertical-align: middle;
    }

    &__time {
      max-width: 100%;
      font-size: 15px;
      font-weight: 400;
      color: #888891;
    }

    &__msg {
      position: relative;
      max-width: 100%;
      padding-top: 13px;
      padding-bottom: 3px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;

      &_pdg {
        padding-right: 50px;
      }

      &_check {
        position: relative;

        &:after {
          content: "";
          position: absolute;
          right: 0;
          bottom: 4px;
          background: url("../images/check.png") no-repeat;
          width: 24px;
          height: 14px;
        }

      }

    }

    &__img-inner {
      white-space: nowrap;
      //overflow: hidden;
      vertical-align: middle;
    }

    &__photo-inner {
      position: relative;
      display: inline-block;
      width: 24px;
      margin-right: 5px;
      vertical-align: middle;

      & > img {
        position: absolute;
        top: -14px;
        border-radius: 50%;
        width: 24px;
        height: 24px;
      }
    }

    &__name-inner {
      font-size: 15px;
      font-weight: 400;
      color: #888891;
      white-space: nowrap;
      overflow: hidden;
      vertical-align: middle;
    }

    &__text-inner {
      max-width: 100%;
      font-size: 15px;
      font-weight: 400;
      color: #1b1b27;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      vertical-align: middle;
    }

    &__counter-inner {
      position: absolute;
      padding: 3px 8px 2px 8px;
      right: 0;
      font-size: 15px;
      font-weight: 500;
      color: #fff;
      background: #f64a35;
      border-radius: 20px;
      z-index: 1;
    }

  }

  &__cr {
    display: block;
    border-collapse: collapse;
    position: relative;
    width: calc(100% - 400px);
    margin-left: 400px;
    height: 100%;
  }

  &__input {
    //display: table-row;
    //vertical-align: bottom;
    //position: relative;
    position: absolute;
    bottom: 0;
    height: auto;
    width: 100%;
    bottom: 0;
    border-top: 1px solid #eaeaed;
  }

  &__input-text {
    width: 100%;
    max-height: 200px;
    padding: 23px 120px 22px 30px;
    font-size: 15px;
    font-weight: 400;
    line-height: normal;
    color: #1b1b27;
    cursor: pointer;
    overflow-y: auto;
    overflow-x: hidden;
    word-wrap: break-word;
    transition: all .2s;

    &:hover {
      background: #fbfbfc;
    }

  }

  &__input-emoji {
    position: absolute;
    right: 79px;
    bottom: 23px;
    width: 18px;
    height: 18px;
    background: url("../images/emoji.png");
    cursor: pointer;
  }

  &__input-send {
    position: absolute;
    right: 31px;
    bottom: 23px;
    width: 18px;
    height: 18px;
    background: url("../images/send.png");
    cursor: pointer;
  }

  &__dialog-wrap {
    position: relative;
    display: block;
    height: calc(100% - 65px);
    overflow: auto;
  }

  &__dialog {
    display: table;
    width: 100%;
    bottom: 0;
    height: 100%;
    vertical-align: bottom;

    &_scr {
      display: table-cell;
      vertical-align: bottom;
      padding-bottom: 30px;

    }

    &__msg {
      position: relative;
      padding: 10px 0;
    }

    &__msg-photo {
      position: absolute;
      top: 5px;
      left: 25px;

      & > img {
        border-radius: 50%;
        width: 38px;
        height: 38px;
        vertical-align: top;
      }
    }

    &__msg-name {
      padding: 0 85px 12px;
      font-size: 15px;
      font-weight: 500;
      color: #1b1b27;
    }

    &__msg-content {
      padding: 0 85px;
      font-size: 15px;
      font-weight: 400;
      line-height: normal;
      word-wrap: break-word;
      color: #1b1b27;

      & img {
        display: block;
        padding: 20px 0;
      }

    }

    &__msg-time {
      position: absolute;
      top: 20px;
      right: 30px;
      font-size: 15px;
      font-weight: 400;
      color: #888891;
    }

  }

}
