.footer {
  //position: absolute;
  bottom: 0;
  width: 100%;
  //min-width: 1400px;
  height: 326px;
  padding-bottom: 45px;
  background-color: #fff;
  border-top: 1px solid #d9d9da;

  &__partner {
    background: #fbfbfc;
    border-bottom: 1px solid #e9e9ec;

    &_center {
      float: left;
      //margin: 0 110px;
      width: calc(100% - 360px);

      &:after {
        clear: both;
        display: table;
        content: " ";
      }

    }

  }

  &__row {

    &:after {
      clear: both;
      display: table;
      content: " ";
    }

  }

  &__partner-item {
    float: left;
    padding: 20px 0;

    &_1 {
      width: 180px;
    }

    &_2 {
      text-align: center;
      width: 50%;
    }

    &_3 {
      text-align: center;
      width: 50%;
    }

    &_4 {
      text-align: right;
      width: 180px;
    }

    & img {
      width: 80px;
      height: auto;
    }

  }

  &__menu-box {
    float: left;
    width: 100%;

    &:after {
      clear: both;
      display: table;
      content: " ";
    }

  }

  &__menu {
    float: right;
    list-style: none;
    margin: 0;
    padding: 50px 0 0 0;

    & li {
      display: inline-block;
      padding: 0 15px;

      &:first-child {
        padding-left: 0;
      }

      &:last-child {
        padding-right: 0;
      }

      & a {
        display: block;
        font-size: 15px;
        font-weight: 400;
        color: #8b8a94;
        text-decoration: none;
        transition: all .2s;

        &:hover {
          color: #36364e;
        }

      }

    }

  }

  &__counter {
    float: left;
    width: 50%;
    //padding-top: 25px;
  }

  &__counter-item {
    float: left;
    padding-right: 45px;
  }

  &__counter-number {
    display: block;
    font-size: 22px;
    font-weight: 500;
    color: #676771;
  }

  &__counter-name {
    display: block;
    padding-top: 10px;
    font-size: 15px;
    font-weight: 400;
    color: #8b8a94;
  }

  &__social-lang {
    float: left;
    width: 50%;
    padding-top: 25px;
  }

  &__social {
    float: right;

    & a {
      display: inline-block;
      position: relative;
      text-decoration: none;
      margin-right: 20px;

      &:last-child {
        margin-right: 0;
      }

      & img {
      }

    }

  }

  &__lang {
    margin-left: 30px;
    display: inline-block;
    float: right;
    cursor: pointer;

    &-item {
      position: relative;
      padding-right: 20px;

      &:after {
        content: "";
        position: absolute;
        top: 5px;
        right: 0;
        background: url("../images/drop-down-arrow.png");
        background-size: 10px 10px;
        width: 10px;
        height: 10px;
      }

      & > span {
        font-size: 15px;
        font-weight: 400;
        color: #8b8a94;
        transition: all .2s;

        &:hover {
          color: #36364e;
        }

      }

    }

  }

  &__copyright {
    float: left;
    width: 100%;
    padding-top: 25px;
    text-align: right;
    font-size: 15px;
    font-weight: 400;
    color: #8b8a94;
  }

}
