.header {
  width: 100%;
  height: auto;
  background: #1b1b27;

  &:after {
    clear: both;
    display: table;
    content: " ";
  }

  &__logo {
    float: left;

    &_img {
      display: inline-block;
      float: left;
      padding: 16px 21px 16px 21px;
      border-right: 1px solid #4c4c4c;
    }

    &_text {
      display: inline-block;
      float: left;
      padding: 26px 41px;
      border-right: 1px solid #4c4c4c;
    }

  }

  &__nav {
    margin-left: 257px;

    &:after {
      clear: both;
      display: table;
      content: " ";
    }

  }

  &__menu {
    float: left;
    list-style: none;
    margin: 0;
    padding: 0 40px;
    width: auto;

    & li {
      display: inline-block;

      & a {
        display: block;
        padding: 27px 15px 26px 15px;
        font-size: 15px;
        font-weight: 400;
        color: #a0a0a9;
        text-decoration: none;
        transition: all .2s;

        &:hover {
          color: #fff;
          background: #36364e;
        }

      }

    }

  }

  &__menu-right {
    float: right;
  }

  &__call {
    float: left;
    padding: 26px 15px 21px;
    cursor: pointer;
    transition: all .2s;

    &:hover {
      background: #36364e;
    }

  }

  &__msg {
    float: left;
    padding: 28px 15px 24px;
    cursor: pointer;
    transition: all .2s;

    &:hover {
      background: #36364e;
    }

  }

  &__user {
    position: relative;
    float: left;
    vertical-align: middle;
    cursor: pointer;
    user-select: none;
    transition: all .2s;

    &:hover {
      background: #36364e;
    }

    & > span {
      display: inline-block;
      margin: 27px 63px 26px 15px;
      font-size: 15px;
      font-weight: 500;
      color: #fff;
    }

    & > img {
      position: absolute;
      top: 16px;
      right: 10px;
      border-radius: 50%;
      width: 38px;
      height: 38px;
    }

  }

  &__slide-bar {
    float: left;
    padding: 28px 30px 24px 15px;
    cursor: pointer;
    transition: all .2s;

    &:hover {
      background: #36364e;
    }

  }

}
